import React, { useState, useEffect } from "react";
import "./Details.css";

function Details({ selectedPlanet, apiKey, onDelete, onEdit }) {
  const [planetDescription, setPlanetDescription] = useState("");

  const fetchPlanetData = () => {
    const planetUrl = `https://api.api-ninjas.com/v1/planets?name=${selectedPlanet.name}`;
    fetch(planetUrl, {
      headers: { "X-Api-Key": apiKey },
    })
      .then((response) => response.json())
      .then((data) => {
        if (data && data.length > 0) {
          const info = data[0];
          setPlanetDescription(`
            Name: ${info.name}
            Mass: ${info.mass}
            Radius: ${info.radius}
            Period: ${info.period}
            Semi Major Axis: ${info.semi_major_axis}
            Temperature: ${info.temperature}
            Distance Light Year: ${info.distance_light_year}
            Host Star Mass: ${info.host_star_mass}
            Host Star Temperature: ${info.host_star_temperature}
          `);
        } else {
          setPlanetDescription("No data available for this planet.");
        }
      })
      .catch((error) => {
        console.error("Error fetching planet data:", error);
        setPlanetDescription("Error fetching planet data.");
      });
  };

  useEffect(() => {
    fetchPlanetData(); // Chama a função sempre que o planeta selecionado muda
  }, [selectedPlanet]);

  return (
    <section id="details" className="details-section">
      <h1>Details</h1>
      <p>{planetDescription}</p>
      <h1 id="title">{selectedPlanet.name}</h1>
      <button onClick={() => onEdit(selectedPlanet)}>Edit</button>
      <button onClick={() => onDelete(selectedPlanet?.name)}>Delete</button>
    </section>
  );
}

export default Details;
