import React, { useState, useEffect } from "react";
import "./PlanetForm.css";

// Funções para gerar dados fictícios
const generateRandomName = () => {
  const names = ["Zygos", "Tessara", "Vulcan", "Triton", "Neptus"];
  return names[Math.floor(Math.random() * names.length)];
};

// Função com ícone predefinida
const randomIcon = () => "https://via.placeholder.com/40";

//Função com imagem predefinida
const randomBackgroundImage = () =>
  "https://via.placeholder.com/1600x900/000000";

function PlanetForm({ onAdd, onEdit, isEditing, planetToEdit, onCancel }) {
  const [formData, setFormData] = useState({
    name: "",
    img: "",
    icon: "",
  });

  // Preenche automaticamente o formulário
  useEffect(() => {
    if (isEditing && planetToEdit) {
      setFormData(planetToEdit);
    } else {
      setFormData({
        name: generateRandomName(),
        img: randomBackgroundImage(),
        icon: randomIcon(),
      });
    }
  }, [isEditing, planetToEdit]);

  // Atualiza os campos do formulário
  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData({ ...formData, [name]: value });
  };

  // Submeter o formulário
  const handleSubmit = (e) => {
    e.preventDefault();
    if (isEditing) {
      onEdit(formData); // Editar planeta
    } else {
      onAdd(formData); // Adicionar planeta
    }
  };

  return (
    <div className="planet-form-container">
      <form onSubmit={handleSubmit} className="planet-form">
        <h2>{isEditing ? "Edit Planet" : "Add Planet"}</h2>
        <input
          type="text"
          name="name"
          placeholder="Planet Name"
          value={formData.name}
          onChange={handleChange}
          required
        />
        <input
          type="text"
          name="img"
          placeholder="Background Image URL"
          value={formData.img}
          onChange={handleChange}
        />
        <input
          type="text"
          name="icon"
          placeholder="Icon URL"
          value={formData.icon}
          onChange={handleChange}
        />
        
        {/* Botão Guardar */}
        <button type="submit" id="submit" className="submit-button">
          {isEditing ? "Save Changes" : "Add Planet"}
        </button>

        {/* Botão Cancelar */}
        <button type="button" id="cancel" onClick={onCancel} className="cancel-button">
          Cancel
        </button>
      </form>
    </div>
  );
}

export default PlanetForm;
