import React from "react";
import "./Navbar.css";

function Navbar({ planets, onPlanetSelect, onToggleForm, onPlanetDelete, selectedPlanet, isEditing }) {
  return (
    <div className="planet-sidebar">
      {/* Exibe os planetas */}
      {planets.map((planet, index) => (
        <div key={index} className="planet-item">
           {/* Botão para selecionar o planeta */}
          <button
            onClick={() => onPlanetSelect(planet)}
            className={`planet-item ${
              selectedPlanet?.name === planet.name ? "active-planet" : ""
            }`}
            style={{
              background: 'none',
              border: 'none',
              cursor: 'pointer',
            }}
          >
            <img src={planet.icon} alt={planet.name} />
          </button>

          {/* Botão 'x' visível no modo de edição */}
          {isEditing && (
            <button
              className="delete-button"
              onClick={() => onPlanetDelete(planet.name)} // Remove o planeta
              title="Delete Planet"
            >
              X
            </button>
          )}
        </div>
      ))}

       {/* Botão '+' para adicionar planeta (só no modo de edição) */}
      {isEditing && (
      <button onClick={onToggleForm} className="add-planet-button">
        +
      </button>
      )}
    </div>
  );
}

export default Navbar;