import React, { useState } from "react";
import "./App.css";
import { planets as initialPlanets, apiKey } from "./data"; // Dados iniciais e API Key
import Navbar from "./Navbar";
import Details from "./Details";
import PlanetForm from "./PlanetForm";
import LogoImg from "./assets/logo/logo.png"

function App() {
  const [planets, setPlanets] = useState(initialPlanets); // Lista de planetas
  const [selectedPlanet, setSelectedPlanet] = useState(planets[0]); // Planeta atual
  const [isEditing, setIsEditing] = useState(false); // Modo de edição
  const [planetToEdit, setPlanetToEdit] = useState(null); // Planeta a ser editado
  const [isFormVisible, setIsFormVisible] = useState(false); // Controla visibilidade do formulário

    // Alternar o modo de edição
    const toggleEditing = () => {
      setIsEditing(!isEditing);
    };

  // Adicionar um novo planeta
  const addPlanet = (newPlanet) => {
    setPlanets([...planets, newPlanet]); // Adiciona o novo planeta à lista
    setSelectedPlanet(newPlanet); // Seleciona o novo planeta automaticamente
  };

  // Editar planeta
  const editPlanet = (updatedPlanet) => {
    setPlanets(
      planets.map((planet) =>
        planet.name === updatedPlanet.name ? updatedPlanet : planet
      )
    );
    setSelectedPlanet(updatedPlanet); // Seleciona o planeta editado
    setIsEditing(false); // Fecha o modo de edição
  };

  // Eliminar planeta
  const deletePlanet = (planetName) => {
    const updatedPlanets = planets.filter((planet) => planet.name !== planetName);
    setPlanets(updatedPlanets); // Remove o planeta da lista
    setSelectedPlanet(updatedPlanets[0] || null); // Seleciona outro planeta
  };

  // Abrir o formulário para editar
  const openEditForm = (planet) => {
    setPlanetToEdit(planet);
    setIsEditing(true);
  };

  // Alterna a visibilidade do formulário
  const toggleFormVisibility = () => {
    setIsFormVisible(!isFormVisible);
  };

  // Fecha o formulário
  const closeForm = () => {
    setIsFormVisible(false);
  };

  return (
    <div>
       {/* Botão para alternar o modo de edição */}
       <button className="toggle-edit-button" onClick={toggleEditing}>
        {isEditing ? "Exit Edit Mode" : "Edit Planets"}
      </button>

      {/* Navbar com o estado do modo de edição */}
      <Navbar
  planets={planets}
  onPlanetSelect={setSelectedPlanet}
  selectedPlanet={selectedPlanet}
  onToggleForm={toggleFormVisibility} 
  onPlanetDelete={deletePlanet} // Passa a função para eliminar planeta
  isEditing={isEditing} // Passa o estado isEditing para o Navbar
/>

      <section
        className="page"
        style={{ backgroundImage: `url(${selectedPlanet.img})`, }}
      >
        <div className="main-content">
          <div className="logo">
            {<img src={LogoImg} id="main-logo" alt="Main Logo" />}
          </div>
          <Details
            selectedPlanet={selectedPlanet}
            apiKey={apiKey}
            onDelete={deletePlanet}
            onEdit={openEditForm}
          />
        </div>
      </section>

      {/* Renderiza o formulário */}
      {isFormVisible && (
        <div className="modal-overlay">
          <div className="planet-form-container">
            <PlanetForm
              onAdd={addPlanet}
              onEdit={editPlanet}
              isEditing={isEditing}
              planetToEdit={planetToEdit}
              onCancel={closeForm}
            />
          </div>
        </div>
      )}
    </div>
  );
}

export default App;
